}

.info-box {
  display: block;
  min-height: 100px;
  background-color: #1276e4;
  width: 100%;
  margin-bottom: 20px;
  padding: 1.25rem;
  border-radius: 0px;
  box-shadow: 0px 5px 20px rgb(0 0 0 / 5%);
}
.info-box1{
  background-color: rgb(105, 149, 16);
}
.info-box2{
  background-color: #169933;
}
.info-box3{
  background-color: rgb(211, 67, 41);
}
.info-box4{
  background-color: #998b11;
}
.info-box5{
  background-color: #ec782a;
}
 
.info-box7{
  background-color: #139e5d;
}
 
.info-box-icon.push-bottom {
  margin-top: 0px;
}

.info-box-icon {
  display: block;
  float: left;
  height: 70px;
  width: 70px;
  text-align: center;
  font-size: 30px;
  line-height: 74px;
}

.rounded {
  border-radius: .25rem!important;
}

.info-box-icon > img {
  max-width: 100%;
}

.info-box-content {
  padding: 10px 10px 10px 0;
  margin-left: 90px;
  color: white;
}

.changeColor{
  color: #b8c7d8 !important;
}

.info-box-text, .progress-description {
  display: block;
  font-size: 1.143rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 500;
}

.info-box-number {
  font-weight: 300;
  font-size: 1.5714285714rem;
}

.info-box-text{
  font-weight: bold;
  font-size: 13px !important;
}


.react-sweet-progress-line-inner{
  position: relative;
  min-height: 2px !important;
  border-radius: 0px !important;
  transition: width 0.3s ease;
}

.react-sweet-progress-line{
  border-radius: 0px !important;
}

.react-sweet-progress-symbol{
  display: none !important;
}

@media screen and (max-width: 900px) {
.set_login_width_style{
  width: 50% !important

}
}

@media screen and (max-width: 700px) {
  .set_login_width_style{
    width: 70% !important
  
  }
  }


