  border: 15px solid transparent;
  border-top: none;
  border-bottom-width: 8px;
  border-left-color: #f53e82;
  border-right-color: #f53e82;
  position: absolute;
  bottom: -8px;
  right: 0;
  -webkit-transform: scale(1.0001);
  -moz-transform: scale(1.0001);
}

.sticky-post:hover {
