  transition: all 0.2s ease;
  display: none;
}

.gallery-item a:hover:before,
.gallery-item-masonry a:hover:before,
.gallery-item-thumnails a:hover:before,
.gallery-item-coinz a:hover:before {
  border: solid 10px #f53e82;
