/*	15. SLick Slider
/* -------------------------------------------------------------------------------- */

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: "";
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Arrows */

.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
  z-index: 999;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
  background: transparent;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before,
.slick-next:before {
  font-family: "FontAwesome";
  font-size: 24px;
  line-height: 1;
  opacity: 0.75;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: all 0.2s ease;
}

.carousel-slider .slick-prev:before,
.carousel-slider .slick-next:before {
  color: #fff;
}

.slick-prev:hover:before,
.slick-next:hover:before {
  color: #f53e82;
}

.slick-prev {
  left: 25px;
}

[dir="rtl"] .slick-prev {
  right: 25px;
  left: auto;
}

.slick-prev:before {
  content: "\f053";
}

[dir="rtl"] .slick-prev:before {
  content: "\f053";
}

.slick-next {
  right: 25px;
}

[dir="rtl"] .slick-next {
  right: auto;
  left: 25px;
}

.slick-next:before {
  content: "\f054";
}

[dir="rtl"] .slick-next:before {
  content: "\f054";
}

.carousel-thumbnail .slick-prev {
  left: 0;
}

.carousel-thumbnail .slick-next {
  right: 0;
}

.carousel-thumbnail .slick-slide {
  cursor: pointer;
  opacity: 0.7;
  padding: 0 10px;
  position: relative;
  transition: all 0.2s ease;
  position: relative;
}

.carousel-thumbnail .slick-slide span {
  position: relative;
  width: 100%;
  float: left;
  display: block;
  border-radius: 3px;
  overflow: hidden;
}

.carousel-thumbnail .slick-slide span:before {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  content: "";
  border: solid 6px #f53e82;
  opacity: 0;
  transition: all 0.2s ease;
}

.carousel-thumbnail .slick-slide:focus {
  outline: none;
}

.carousel-thumbnail .slick-slide:hover,
.carousel-thumbnail .slick-slide.slick-current {
  opacity: 1;
}

.carousel-thumbnail .slick-slide:hover span:before,
.carousel-thumbnail .slick-slide.slick-current span:before {
  opacity: 1;
}

.carousel-slider img {
  padding: 0;
  border: none;
  width: 100%;
}

.carousel-thumbnail {
  margin-top: 30px;
  padding: 0 35px;
}

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: "â€¢";
  text-align: center;
  opacity: 0.25;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  opacity: 0.75;
  color: black;
}

