/*	29. Progress Circle
/* -------------------------------------------------------------------------------- */

@-webkit-keyframes load {
  0% {
    stroke-dashoffset: 0;
  }
}

@keyframes load {
  0% {
    stroke-dashoffset: 0;
  }
}

.coinz-progress-circle {
  position: relative;
  /*display: inline-block;*/
  padding: 0;
  text-align: center;
}

.coinz-progress-circle > span.progress-block {
  display: inline-block;
  position: relative;
  text-align: center;
  color: #93a2ac;
  font-family: Lato;
  font-weight: 100;
  margin: 2rem 0;
}

.coinz-progress-circle > .progress-block:before {
  content: attr(data-name);
  position: absolute;
  width: 100%;
  bottom: -2rem;
  font-weight: 400;
  color: #737373;
  font-family: "Ubuntu", sans-serif;
}

.coinz-progress-circle > .coinz-progress-circle-precentage {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  left: 0;
  font-size: 2rem;
  text-align: center;
  color: #737373;
  font-family: "Ubuntu", sans-serif;
  font-size: 24px;
}

.coinz-progress-circle svg {
  width: 10rem;
  height: 10rem;
}

.coinz-progress-circle svg:nth-child(2) {
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

.coinz-progress-circle svg:nth-child(2) path {
  opacity: 0;
}

.coinz-progress-circle.animated svg:nth-child(2) path {
  fill: none;
  stroke-width: 25;
  stroke-dasharray: 629;
  opacity: 0.9;
  -webkit-animation: load 2s;
  animation: load 2s;
}

