.circle-clipper.left .circle {
  left: 0;
  border-right-color: transparent !important;
  -webkit-transform: rotate(10deg);
  transform: rotate(10deg);
}

.circle-clipper .circle:after {
  content: "";
  width: 6px;
  height: 6px;
  display: block;
  position: absolute;
  border-radius: 6px;
  background-color: #666;
}

