}

.canvas .foreground {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-size: 10%;
  color: white;
  text-align: center;
  padding-top: 20px;
  font-size: 20px;
}

.canvas .sky {
  height: 300%;
  width: 300%;
  position: absolute;
  top: -100%;
  left: -100%;
  margin-top: 50%;
  -webkit-animation: rotate 250s infinite linear;
  -webkit-backface-visibility: hidden;
  -webkit-transform-style: preserve-3d;
}

.star-blink {
  position: absolute;
  width: 7px;
  height: 7px;
  background-color: #b6cbd4;
  border-radius: 50%;
  z-index: 0;
  -webkit-transform: rotate(0deg) scale(0.08);
  -webkit-backface-visibility: hidden;
  -webkit-animation: blinkAfter 15s infinite ease-out;
}

.star-blink div {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -webkit-transform: rotate(45deg) scale(0.75);
  box-shadow: 0 0 2px 8px rgba(0, 0, 0, 0.4),
    0 0 2px 9px rgba(255, 255, 255, 0.07), 0 0 2px 15px rgba(0, 0, 0, 0.4),
    0 0 2px 16px rgba(255, 255, 255, 0.1);
  -webkit-backface-visibility: hidden;
}

.star-blink:after,
.star-blink:before,
.star-blink div:after,
.star-blink div:before {
  content: "";
  position: absolute;
  display: block;
  top: -350%;
  left: 45%;
  height: 800%;
  width: 10%;
  border-radius: 50%;
  background-color: #b6cbd4;
  background: linear-gradient(
    rgba(255, 255, 255, 0.1) 0%,
    rgba(255, 255, 255, 0.7) 50%,
    rgba(255, 255, 255, 0.1) 100%
